import { position } from '../../Core';

function Astar(startCallback, speed) {
    var retSearchPath = [];
    var retBombPath = [];
    var retShortestPath = [];

    if (position.bomb) {
        retShortestPath = retShortestPath.concat(DoAStar(position.start, position.bomb, retSearchPath))

        // 
        if (retShortestPath.length > 0) {
            retShortestPath = retShortestPath.concat(DoAStar(position.bomb, position.end, retBombPath))
        }
    } else {
        retShortestPath = retShortestPath.concat(DoAStar(position.start, position.end, retSearchPath))
    }
    //  start 
    startCallback(retSearchPath, retShortestPath, speed, retBombPath);
}

// update search
function DoAStar(startPos, endPos, searchPath) {
    // 1
    var i, j;
    var weights = {}
    for (i = 0; i < position.rowSize; i++) {
        for (j = 0; j < position.colSize; j++) {
            weights[[i, j]] = 0;
        }
    }

    var unvisited = [];
    var table = {};

    // table: 0previous vertexnull
    // queue
    for (i = 0; i < position.rowSize; i++) {
        for (j = 0; j < position.colSize; j++) {
            var pos = [i, j];

            // posdirectiontotal
            table[pos] = [Infinity, null, null, Infinity];
        }
    }
    table[startPos] = [0, null, "up", 0]; // 
    unvisited.push(startPos); // queue

    /*  */
    var isFoundEnd = false;
    var visited = new Set();
    searchPath.push([startPos]); // 
    while (unvisited.length > 0) {
        // 1. 
        var curPos = GetClosestNode(table, unvisited, endPos);

        if (curPos in position.wall) {
            continue; // 
        }

        // 2. 
        var up = curPos[0] - 1 >= 0 ? [curPos[0] - 1, curPos[1]] : null;
        var right = curPos[1] + 1 < position.colSize ? [curPos[0], curPos[1] + 1] : null;
        var down = curPos[0] + 1 < position.rowSize ? [curPos[0] + 1, curPos[1]] : null;
        var left = curPos[1] - 1 >= 0 ? [curPos[0], curPos[1] - 1] : null;

        // eslint-disable-next-line
        [up, right, down, left].forEach((nextPos, idx) => {
            if (!nextPos || isFoundEnd || nextPos in position.wall) return; //  or  or 

            //  ++
            // unvisitedcontinue
            var total = table[curPos][0] + weights[nextPos] + GetScore(table[curPos][2], idx);
            if (total < table[nextPos][0]) {
                table[nextPos][0] = total;
                table[nextPos][3] = table[nextPos][0] + GetHeuristic(nextPos, endPos);
                table[nextPos][1] = curPos;
                switch (idx) {
                    case 0:
                        table[nextPos][2] = "up";
                        break;
                    case 1:
                        table[nextPos][2] = "right";
                        break;
                    case 2:
                        table[nextPos][2] = "down";
                        break;
                    case 3:
                        table[nextPos][2] = "left";
                        break;
                    default:
                        break;
                }
            }

            if (!visited.has(curPos.toString())) {
                unvisited.push(nextPos);
            }

            if (nextPos.toString() === endPos.toString()) { // 
                isFoundEnd = true;
            }
        })
        if (!visited.has(curPos.toString())) {
            searchPath.push([curPos]); // 
            visited.add(curPos.toString()); // 
        }

        if (isFoundEnd) { // 
            break;
        }
    }

    // 
    var curShortestPath = []
    if (isFoundEnd) {
        curPos = endPos;
        while (curPos) { // startprevious vertexnull
            curShortestPath.unshift(curPos);
            curPos = table[curPos][1];
        }
    }
    return curShortestPath;
}

// 
function GetHeuristic(startPos, endPos) {
    return Math.abs(endPos[0] - startPos[0]) + Math.abs(endPos[1] - startPos[1]);
}

//  +++
function GetClosestNode(table, unvisited, endPos) {
    let retPos, retIndex;
    for (var i = 0; i < unvisited.length; i++) {
        if (!retPos || table[retPos][3] > table[unvisited[i]][3]) {
            retPos = unvisited[i];
            retIndex = i;

            //  
        } else if (table[retPos][3] === table[unvisited[i]][3]) {
            if (GetHeuristic(retPos, endPos) > GetHeuristic(unvisited[i], endPos)) {
                retPos = unvisited[i];
                retIndex = i;
            }
        }
    }

    unvisited.splice(retIndex, 1);
    return retPos;
}

function GetScore(direction, index) {
    var score = 0;
    switch (direction) {
        case "up":
            switch (index) {
                case 0:
                    score = 1;
                    break;
                case 1:
                    score = 2;
                    break;
                case 2:
                    score = 3;
                    break;
                case 3:
                    score = 2;
                    break;
                default:
                    break;
            }
            break;
        case "right":
            switch (index) {
                case 0:
                    score = 2;
                    break;
                case 1:
                    score = 1;
                    break;
                case 2:
                    score = 2;
                    break;
                case 3:
                    score = 3;
                    break;
                default:
                    break;
            }
            break;
        case "down":
            switch (index) {
                case 0:
                    score = 3;
                    break;
                case 1:
                    score = 2;
                    break;
                case 2:
                    score = 1;
                    break;
                case 3:
                    score = 2;
                    break;
                default:
                    break;
            }
            break;
        case "left":
            switch (index) {
                case 0:
                    score = 2;
                    break;
                case 1:
                    score = 3;
                    break;
                case 2:
                    score = 2;
                    break;
                case 3:
                    score = 1;
                    break;
                default:
                    break;
            }
            break;
        default:
            break;
    }
    return score;
}

export default Astar;