import { position } from '../../Core';
import { CreateAround } from './Core';

const direction = {
    horizontal: 0,
    vertical: 1
}

function RecursiveDivision_Horizontal() {
    var walls = CreateAround();

    // truefalse
    // 
    DoRecursiveDivision(0, 0, position.colSize, position.rowSize, walls, true);
    return walls;
}

// 
// horizontal skew
function DoRecursiveDivision(x, y, width, height, walls, boolDirection) {
    // +
    // 3
    if (width < 4 || height < 4) {
        return;
    }

    // random
    var curDirection = null;
    if (boolDirection) curDirection = direction.horizontal;
    else curDirection = direction.vertical;

    var door;
    if (curDirection === direction.horizontal) {
        // height=11x=x0x=10
        //  x 2~8 ()

        // xx0+2random0~6
        var randomX = x + 2 + Random_0_to_n(height - 4);

        // width = 9y=y0y=8
        //  y0+ 1~7random0~6
        door = y + 1 + Random_0_to_n(width - 3);

        //  y0~y0+width
        for (var _y = y; _y < y + width; _y++) {
            if (_y !== door) {
                walls.push([randomX, _y]);
            }
        }

        //  x,y,width  height-x0
        DoRecursiveDivision(x, y, width, randomX - x, walls, true); // 

        //  x, y, width  height  - ()
        DoRecursiveDivision(randomX, y, width, height - (randomX - x), walls, false); // 
    } else {
        // 
        var randomY = y + 2 + Random_0_to_n(width - 4);
        door = x + 1 + Random_0_to_n(height - 3);

        for (var _x = x; _x < x + height; _x++) {
            if (_x !== door) {
                walls.push([_x, randomY]);
            }
        }

        DoRecursiveDivision(x, y, randomY - y, height, walls, true); // 
        DoRecursiveDivision(x, randomY, width - (randomY - y), height, walls, false); // 
    }
}

// 
function Random_0_to_n(n) {
    return Math.floor(Math.random() * n / 2) * 2;
}

export default RecursiveDivision_Horizontal;